import React from "react";
import SideBar from "../commonComponent/sideBar";
import Header from "../commonComponent/header";
import { Link, useLocation, useNavigate } from "react-router-dom";
import { useForm } from "react-hook-form";
import { useAlert } from "react-alert";
import { showAlert } from "../commonComponent/alertManager";
import { useQuery } from "@tanstack/react-query";
import {
  addDeals,
  restaurantList,
} from "../../apiServices/home/homeHttpService";

function AddDeal() {
  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm({
    mode: "onChange",
  });

  const alert = useAlert();
  const navigate = useNavigate();
  const location = useLocation();
  const queryParams = new URLSearchParams(location.search);

  const { data: restaurant } = useQuery({
    queryKey: ["restaurantList"],
    queryFn: restaurantList,
    onError: (error) => {
      console.log(error);
    },
    select: (data) => data.results.list,
  });

  const onSubmit = async (data) => {
    if (!data.restaurantName_en) {
      data.restaurantName_en = restaurant?.[0]?._id || "";
    }
    const response = await addDeals(data);
    if (!response.error) {
      navigate(`/admin/deal-management?tab=${queryParams.get("tab") || 1}`);
      showAlert(alert, response.message, { timeout: 3000 });
      reset();
    } else {
      showAlert(alert, response.message, { timeout: 3000 });
    }
  };
  return (
    <>
      <Header />
      <div className="admin_main">
        <SideBar />
        <div className="admin_contentpart">
          <Link
            to={`/admin/deal-management?tab=${queryParams.get("tab") || 1}`}
            className="comman_btn w-fit"
          >
            <i className="fa fa-arrow-left" />
          </Link>

          <div className="row comman_design mx-0 mt-4">
            <div className="col-12">
              <div className="row comman_table pt-md-4 pb-md-4 pb-3 px-md-4 px-2">
                <h3 className="mb-3 fs-5 fw-semibold">Add Deal</h3>
                <form className="form-design" onSubmit={handleSubmit(onSubmit)}>
                  <div className="row">
                    <div className="col-md-6">
                      <div className="form-group">
                        <label htmlFor="form-label">Restaurant Name</label>
                        <select
                          className="form-select"
                          name="restaurantName_en"
                          id="restaurantName_en"
                          {...register("restaurantName_en", {
                            required: false,
                          })}
                        >
                          {errors.restaurantName_en &&
                            errors.restaurantName_en.type === "required" && (
                              <p className="form-error">
                                This field is required
                              </p>
                            )}
                          {restaurant?.map((item, index) => (
                            <option key={index} value={item._id}>
                              {item.name}
                            </option>
                          ))}
                        </select>
                      </div>
                    </div>
                    <div className="col-md-6">
                      <div className="form-group">
                        <label htmlFor="form-label">Restaurant Location</label>
                        <div className="location_mark">
                          <input
                            type="text"
                            className="form-control"
                            name="location"
                            id="location"
                            {...register("location", {
                              required: true,
                            })}
                          />
                          {errors.location &&
                            errors.location.type === "required" && (
                              <p className="form-error">
                                This field is required
                              </p>
                            )}
                          <i className="fa fa-map-marker" />
                        </div>
                      </div>
                    </div>
                    <div className="col-md-6">
                      <div className="form-group">
                        <label htmlFor="form-label">Offer Percentage</label>
                        <input
                          type="number"
                          className="form-control"
                          name="offerPercentage"
                          id="offerPercentage"
                          {...register("offerPercentage", {
                            required: true,
                          })}
                        />
                        {errors.offerPercentage &&
                          errors.offerPercentage.type === "required" && (
                            <p className="form-error">This field is required</p>
                          )}
                      </div>
                    </div>
                    <div className="col-md-6">
                      <div className="form-group">
                        <label htmlFor="form-label">Expire Date</label>
                        <input
                          type="date"
                          className="form-control"
                          name="expireDate"
                          id="expireDate"
                          {...register("expireDate", {
                            required: true,
                          })}
                          min={new Date().toISOString().split("T")[0]}
                        />
                        {errors.expireDate &&
                          errors.expireDate.type === "required" && (
                            <p className="form-error">This field is required</p>
                          )}
                      </div>
                    </div>
                    <div className="col-md-12">
                      <div className="form-group">
                        <label htmlFor="form-label">Description</label>
                        <textarea
                          rows={4}
                          className="form-control"
                          name="description"
                          id="description"
                          {...register("description", {
                            required: true,
                          })}
                        />
                        {errors.description &&
                          errors.description.type === "required" && (
                            <p className="form-error">This field is required</p>
                          )}
                      </div>
                    </div>
                    <div className="col-12">
                      <button type="submit" className="comman_btn w-fit">
                        Add Deal
                      </button>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default AddDeal;
